import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { uSyncSnapshots } from '../constants';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import uSyncSnapshotsWorkspaceContext from './snapshots.context';

@customElement(uSyncSnapshots.element)
export class uSyncSnapshotsElement extends UmbLitElement {
	//@ts-ignore - setup to provide the context from this element
	#context: uSyncSnapshotsWorkspaceContext;

	constructor() {
		super();
		this.#context = new uSyncSnapshotsWorkspaceContext(this);
	}

	render() {
		return html`<umb-workspace-editor .enforceNoFooter=${true}>
			<div slot="header">
				<strong><umb-localize key="uSyncSnapshots_name"></umb-localize></strong><br />
				<em><umb-localize key="uSyncSnapshots_description"></umb-localize></em>
			</div>
		</umb-workspace-editor>`;
	}
}

export default uSyncSnapshotsElement;

declare global {
	interface HTMLElementTagNameMap {
		'usync-snapshot-element': uSyncSnapshotsElement;
	}
}
